        "info" : Imagenes.TenseiShittaraSlimeDattaKen.TenseiShittaraSlimeDattaKen1, 
        "sinopsis" : 'Satoru Mikami es un asalariado normal de 37 años que vive en Tokio. Está casi contento con su vida monótona, a pesar de que no tiene novia. Durante un encuentro casual con su colega, un asaltante aparece de la nada y lo apuñala. Mientras sucumbe a sus heridas, una voz misteriosa resuena en su mente y recita una serie de órdenes que no pudo entender. Después de recuperar la conciencia, Satoru descubre que se ha reencarnado como un Slime en un mundo desconocido. Al mismo tiempo, también adquiere nuevas habilidades, particularmente la habilidad llamada "Depredador", que le permite devorar cualquier cosa e imitar su apariencia y habilidades. Se topa con Veldora Tempest, un "Dragón Tormenta" de nivel catastrófico, que estuvo sellado durante 300 años por reducir una ciudad a cenizas. Sintiendo lástima por él, Satoru se hace amigo del dragón y le promete ayudarlo a destruir el sello. A cambio, Veldora le otorga el nombre Rimuru Tempest para otorgarle protección divina. Con sus nuevos poderes y su sabiduría, Rimuru se gana la amistad y la lealtad de los monstruos que habitan en el Gran Bosque Jura, quienes lo aceptan como su líder y juntos fundan la nación Gran Jura Tempest. A medida que su nueva nación crece rápidamente en fuerza e influencia, Rimuru y sus súbditos atraen la atención de las potencias extranjeras cercanas, desde monarcas y héroes legendarios hasta señores demoníacos, algunos buscan convertirse en sus aliados, mientras que otros pretenden simplemente aprovecharse de ellos o destruirlos completamente por el bien de sus planes.',
